import React, { useState } from 'react';
import 'bulma/css/bulma.css';
import '@fortawesome/fontawesome-free/css/all.css';
import './App.scss';
import { Tabs } from './components/Tabs';

export const tabs = [
  { id: 'tab-1', title: 'Tab 1', content: 'Some text 1' },
  { id: 'tab-2', title: 'Tab 2', content: 'Some text 2' },
  { id: 'tab-3', title: 'Tab 3', content: 'Some text 3' },
];

export const App: React.FC = () => {
  const [clickTabId, setClickTabId] = useState('tab-1');
  const [clickTabTitle, setClickTabTitle] = useState('tab-1');

  const onClick = (tabId: string, tabTitle: string) => {
    setClickTabId(tabId);
    setClickTabTitle(tabTitle);
  };

  return (
    <div className="section">
      <h1 className="title">
        {`Selected tab is ${clickTabTitle}`}
      </h1>

      <Tabs clickTabId={clickTabId} onClick={onClick} tabs={tabs} />
    </div>
  );
};
